.navContainer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: wheat;
  margin: 0 auto;
}

  
@media (min-width:769px) {
  .nav{
    backdrop-filter: blur(20px);
  }
  .navScroll{
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }

}
  @media (max-width:768.98px) {
    .nav{
      position: fixed;
      left: 0;
    right: 0;
    min-height: 100%;
    }
.navContainer{
  width: 100%;
  position: fixed;
  height: 50px;
  justify-content: space-between;
  background:radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);;
  z-index: 1;
}
.navContainer::after{
  content: "";
  position: absolute;
  width: 100%;
  height: 50px;
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);;
  z-index: 300;
}
  }