header {
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
  animation: bg-animation 25s ease-in-out infinite;
  background-position-y: 100px;
  height: 80rem;
}

.mtop{
  margin-top: 26rem!important;
}
.wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 120rem;
  margin: 0 auto;
  margin-top: 20rem;
}
.description {
  color: wheat;
  width: 100%;

}
.description h3 {
  font-size: 5rem;
}
.description h1 {
  font-size: 6rem;
  margin-top: 3rem;
}

.description p {
  font-size: 1.8rem;
  margin-top: 3rem;
  max-width: 50rem;
  word-spacing: 0.5rem;
  line-height: 1.5;
}

.ramka {
  width: 40rem;
  height: 30rem;
  /* border: 8px solid #337AB7; */
  border: 0.7rem solid rgb(168, 138, 83);
  /* background: #bfe2ff; */
  background: rgb(168, 138, 83);
  border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
  /* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
  box-shadow: 15px 15px 50px rgb(168, 138, 83);
  animation: anim-ramka-1 10s infinite;

  font-family: "Roboto", sans-serif;
  overflow: hidden;
}
.ramka img {
  width: 40rem;
  position: relative;
  top: 3rem;
  left: -15%;
}
@media (orientation: portrait) and (max-width: 768.98px) {
  header {
    height: 80rem;
  }
}
@media (orientation: portrait) and (min-width: 769px) {
  header {
    height: 80rem;
  }
}
@media (orientation: landscape) and (max-width: 768.98px) {
  header {
    height: 80rem;
  }
}
@media (orientation: landscape) and (min-height: 500px) and (max-height: 1025px) {
  header {
    height: 80rem;
  }
}
@media (max-width: 768.98px) {
  header {
  }

  .wrapper {
    flex-direction: column;
    align-items: center;
    margin-left: 3rem;
    gap: 10rem;
  }
  .description {
    
  }
  .ramka {
    width: 30rem;
    height: 30rem;
  }
  .ramka img {
    /* left: 0px ; */
  }
}

@keyframes bg-animation {
  0% {
    background-size: 120%;
    background-position: 50% 50%;
  }
  20% {
    background-size: 150%;
    background-position: 0 50%;
  }
  40% {
    background-size: 110%;
    background-position: 20% 80%;
  }
  60% {
    background-size: 160%;
    background-position: 60% 10%;
  }
  80% {
    background-size: 120%;
    background-position: 40% 70%;
  }
  100% {
    background-size: 120%;
    background-position: 50% 50%;
  }
}
@keyframes animStar {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}

@keyframes anim-ramka-1 {
  0% {
    border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
    box-shadow: 15px 15px 50px rgb(168, 138, 83);
  }
  25% {
    border-radius: 58% 42% 75% 25%/76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67%/55% 27% 73% 45%;
    box-shadow: -10px -5px 50px rgb(168, 138, 83);
  }
  75% {
    border-radius: 33% 67% 58% 42%/63% 68% 32% 37%;
  }
}

